#bg-wrap {
  @apply absolute top-0 left-0;
  @apply w-full h-full;
  @apply overflow-y-hidden;
  @apply -z-1;
}

#bg-gradient {
  width: 100%;
  height: 100%;
  min-height: 300rem;
}
